@import "~bulma/sass/utilities/_all.sass";
@import "~bulma/sass/base/helpers.sass";
@import "~bulma/sass/components/list.sass";

.list-item {
    padding: 0.5em 0.5em;
}

.sorting {
    @extend .select-none;

    opacity: 0.7;
    -webkit-box-shadow: 0px 10px 15px -5px rgba(0,0,0,0.75);
    -moz-box-shadow: 0px 10px 15px -5px rgba(0,0,0,0.75);
    box-shadow: 0px 10px 15px -5px rgba(0,0,0,0.75);
}

.select-none {
    -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */
     -khtml-user-select: none; /* Konqueror HTML */
       -moz-user-select: none; /* Old versions of Firefox */
        -ms-user-select: none; /* Internet Explorer/Edge */
            user-select: none; /* Non-prefixed version, currently
                                  supported by Chrome, Opera and Firefox */
}
