@import "~bulma/sass/utilities/_all.sass";
@import "~bulma/sass/elements/button.sass";
@import "~bulma/sass/grid/columns.sass";
@import "~bulma/sass/form/_all.sass";

.input {
    width: auto;
    height: auto;

    &.name {
        width: 8em;
    }

    &.quantity {
        width: 1em;
    }

    &.price {
        width: 3.2em;
    }
}

.clickable {
    cursor: pointer !important;
}
